$mainColor: #46a358;
$darkColor: #3d3d3d;
$grayColor: #727272;
$lightGrayBackgroundColor: #fbfbfb;

$primaryFontLight: 'cera_prolight', 'sans serif';
$primaryFontRegular: 'cera_proregular', 'sans serif';
$primaryFontMedium: 'cera_promedium', 'sans serif';
$primaryFontBold: 'cera_probold', 'sans serif';
$primaryFontBlack: 'cera_problack', 'sans serif';
$secondaryFontRegular: 'encode_sans_expandedregular', 'sans serif';

@mixin before_after($background-image: none, $width, $height: $width) {
  content: '';
  display: inline-block;
  background-image: url($background-image);
  width: $width;
  height: $height;
}
